import React, {useEffect, useState} from 'react'
import noteService from './services/notes'

const Name = ({content, deleteItem}) => {
    console.log(content);
    return(
        <tr>
        <td>{content.name}</td> <td>{content.number}</td>
        <td><button onClick={deleteItem}>delete</button></td>
        </tr>
    )
}

const PhoneBook = () => {
    const [names, setNames] = useState([])
    const [newItem, setNewItem] = useState({
        name:'',
        number: ''
    })
    
    useEffect(()=>{
        noteService
            .getAll()
            .then(response => {
                console.log('get success!');
                setNames(response.data)
            })
    },[])

    console.log("item get from server:",names);

    const handleNameChange = (event) => {
        setNewItem({...newItem,name:event.target.value})
    }

    const handleNumberChange = (event) => {
        setNewItem({...newItem,number:event.target.value})
    }

    const addItem = (event) => {
        event.preventDefault()
        if(newItem.name === ''){
            alert('please input name')
            return
        }
        if(newItem.number === ''){
            alert('please input number')
            return
        }
        else{
            const newItemObject = {...newItem, id: names.length + 1}

            noteService
                .create(newItemObject)
                .then(response => {
                    setNames(names.concat(newItemObject))
                    setNewItem({name:'', number:''})
                })
        }
    }

    // const deleteItemOf = (id) => {

    //     noteService
    //         .deleteItem(id)
    //         .then(response => {
    //             setNames(names.filter(note => note.id !== id))
    //         })
    // }

    console.log('newItem',newItem);

    return (
        <div>
            <h1>PhoneBook</h1>
            <h1>Add a new</h1>
            <form>
                <div>name: 
                    <input 
                        value={newItem.name}
                        onChange={handleNameChange}
                    />
                </div>
            </form>
            <form onSubmit={addItem}>
                <div>number: 
                    <input 
                        value={newItem.number}
                        onChange={handleNumberChange}
                    />
                </div>
            </form>
            <div>
                <button type="submit">add</button>
            </div>
            <h1>Number</h1>
            <table>
                {names.map(
                    name => <Name content={name} />
                )}
            </table>
        </div>
    )

}

export default PhoneBook